
import Vue from "vue";
import { DateTime } from "luxon";

export default Vue.extend({
    name: "ListEntryItem",

    props: [
        "startDate",
        "endDate",
        "title",
        "content",
        "attachments",
        "withEndDate",
    ],

    filters: {
        day: function(dateString) {
            if (!dateString) {
                return "";
            }

            return DateTime.fromISO(dateString)
                .setLocale("de")
                .toFormat("dd");
        },
        month: function(dateString) {
            if (!dateString) {
                return "";
            }

            return DateTime.fromISO(dateString).setLocale("de").monthShort;
        },
    },
});
